@import 'color';

@media screen and (max-width: 1024px) {
  #container {
    flex-direction: column;
  }

  #content {
    width: 100%;
  }

  .aside {
    width: 100%;
  }
}

// For desktop, width over 1024px
@media screen and (min-width: 1025px) {
  #container {
    flex-direction: row;
  }

  #content {
    width: 68%;
  }

  .aside {
    float: right;
    width: 30%;
  }
}

#content {
  float: left;
  padding: 0;
}

/* Contatiner part */
#container {
  display: flex;
  justify-content: space-between;
  margin: 0 10px;
  margin-bottom: 20px;
}

.box-img-title {
  margin-bottom: 20px;
}

.img-title {
  border-radius: 10px 10px 0 0;
  max-height: 350px;
  object-fit: cover;
  object-position: 0 30%;
  width: 100%;
}

.box-content_title {
  margin: auto;
  margin-bottom: 20px;
  width: 100%;
}

.content img {
  border-radius: 5px;
  display: block;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 75%
}

/* line */
.line-dash {
  border: 1px dashed #999999;
}

/* text */
.fix-date{
  padding-left: 30px;
}

.txt-main {
  color: #333333;
  text-indent: 10px;
  margin-top: 0;
}

/* Headings */
#content h1 {
  font-size: 30px;
  color: $color-pink;
  margin-bottom: 0.3em;
}

#content h2 {
  margin-top: 2em;
  color: $color-headings;
  font-size: 25px;
}

#content h3 {
  margin-top: 2em;
  color: $color-headings;
  font-size: 20px;
}

#content h4 {
  margin-top: 2em;
  color: $color-headings;
  font-size: 15px;
}

#content p {
  color: $color-paragraph;
  font-size: 17px;
  font-weight: 400;
  line-height: 1.8;
  text-indent: 1em;
  margin-block-end: 2em;
}

#content li {
  color: $color-paragraph;
  font-size: 17px;
  font-weight: 400;
  line-height: 1.8;
}

#content p a {
  color: $color-pink;
}

#content p a:hover {
  color: $color-pink-bold;
}

#content strong {
  border: solid $color-shadow-pink 1px;
  border-bottom-width: 3px;
  border-radius: 0.3em;
  color: $color-pink-bold;
  font-weight: normal;
  letter-spacing: 0.01em;
  padding: 0.3em 0.5em;
  margin: 0 0.3em;
}

#content time {
  color: $color-gray;
}

.far {
  padding-right: 0.5em;
}

p em {
  display: block;
  color: $color-gray;
  text-align: center;
  font-style: normal;;
}

code {
  font-size: 17px;
}